import React, { useState, useEffect } from 'react';
import { theme } from '../../theme.js'
import { GrLinkedin } from "@react-icons/all-files/gr/GrLinkedin";
import { GrTwitter } from "@react-icons/all-files/gr/GrTwitter";
import { MdArrowOutward } from "react-icons/md";
import Numbers from './numbers.js'

const AboutUs = () => {
  const [selectedFounder, setSelectedFounder] = useState(null);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    handleResize();
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const openPopup = (founder) => {
    setSelectedFounder(founder);
  };

  const closePopup = () => {
    setSelectedFounder(null);
  };

  const containerStyle = {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '20px',
    textAlign: 'center',
  };

  const titleStyle = {
    fontSize: '2rem',
    fontWeight: 'bold',
    // marginBottom: '140px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const foundersContainerStyle = {
    display: 'flex',
    justifyContent: 'center',
    gap: isMobile ? '20px' : '80px',
    // marginBottom: '10px',
    textAlign: 'left',
    flexWrap: isMobile ? 'wrap' : 'nowrap'
  };

  const founderStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    // cursor: 'pointer'
  };

  const imageStyle = {
    width: '150px',
    height: '150px',
    borderRadius: '50%',
    marginBottom: '10px',
    backgroundColor:theme.colors.background,
    boxShadow: '0px 2px 4px rgba(0, 0, 0, 0.1)',
    border: '1px solid #ccc',
  };

  const nameAndTitleContainer = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
    overflow: 'hidden',
  };

  const nameStyle = {
    fontSize: '1.2rem',
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    display: 'flex',
    alignItems: 'center',
  };

  const titleTextStyle = {
    fontSize: '1rem',
    fontFamily: theme.fonts.primary,
    color: theme.colors.primary,
    marginLeft: '8px',
    // backgroundColor:theme.colors.background,
    // boxShadow: '0px 2px 4px rgba(0, 0, 0, 0.1)',
    // padding:'12px',
    // marginTop:'10px'
  };

  const popupOverlayStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 1000,
  };

  const popupContentStyle = {
    backgroundColor: '#fff',
    padding: '20px',
    borderRadius: '8px',
    maxWidth: '500px',
    width: '80%',
    maxHeight: '80%', // Ensure popup content does not exceed 80% of the viewport height
    overflowY: 'auto', // Enable vertical scrolling if content overflows
    textAlign: 'center',
  };

  const popupImageStyle = {
    width: '150px',
    height: '150px',
    borderRadius: '50%',
    marginBottom: '10px'
  };

  const popupNameContainer = {
    display: 'flex',
    flexDirection: 'row', 
    alignItems: 'center', 
    justifyContent:'center', 
    marginTop:'10px'
  }

  const popupNameStyle = {
    fontSize: '1.2rem',
    fontWeight: 'bold',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const popupTitleTextStyle = {
    fontSize: '1rem',
    marginBottom: '10px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary
  };

  const popupDescriptionStyle = {
    fontSize: '0.8rem',
    color: '#555',
    fontFamily: theme.fonts.primary,
  };

  const popupIconsStyle = {
  display: 'flex',
  justifyContent: 'flex-end',
  alignItems: 'center',
  marginTop: '10px',
};

  const closeButtonStyle = {
    marginTop: '20px',
    padding: '10px 20px',
    backgroundColor: '#004ca3',
    color: '#fff',
    border: 'none',
    borderRadius: '5px',
    // cursor: 'pointer'
  };

  const rightSectionStyle = {
    alignItems: 'center',
    gap: '10px',
    marginLeft:'10px'
  };

  const ulStyle = {
    listStylePosition: 'inside',
    padding: '0',
    // marginTop:'25px'
  };

  const liStyle = {
    marginBottom: '5px',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    fontSize: '1rem',
    textAlign: 'left',
    paddingLeft: '1em', // Add padding to align text properly
    textIndent: '-1em', // Negative indent to pull the first line back
    marginLeft: '1em', // Adjust margin to properly align bullets
    // listStylePosition: 'inside', // Ensure bullets are inside the padding
    lineHeight: '1.5',

  };


  return (
    <div style={containerStyle}>
      <p style={titleStyle}>{theme.aboutus.title}</p>
      <Numbers/>
      <p style={{ ...titleStyle, fontSize: '1.2rem', marginBottom:'50px' }}>Leadership Team</p>
      <div style={foundersContainerStyle}>
        {theme.aboutus.founders.map((founder) => (
          <div key={founder.id} style={founderStyle}>
            <img src={founder.image} alt={founder.name} style={imageStyle} />
            <div style={popupNameContainer}>
              <div style={nameStyle}>{founder.name}</div>
              {/*<MdArrowOutward size={25} style={{marginLeft:8, marginBottom: 8}}/>*/}
            </div>
            <div style={titleTextStyle}>{founder.title}</div>
            <div style={popupNameContainer}>
              <a href={founder.linkedin} target="_blank" rel="noopener noreferrer">
                    <GrLinkedin size={25} style={{margin:8, color:theme.colors.primary}}/>
                  </a>
                {founder.twitter &&
                  <a href={founder.twitter} target="_blank" rel="noopener noreferrer">
                    <GrTwitter size={25} style={{marginLeft:16, color:theme.colors.primary}}/>
                  </a>
                }
              </div>
            <ul style={ulStyle}>
              {founder.description.map((point, idx) => (
                <li key={idx} style={liStyle}>{point}</li>
              ))}
            </ul>
          </div>
        ))}
      </div>

      {selectedFounder && (
        <div style={popupOverlayStyle} onClick={closePopup}>
          <div style={popupContentStyle} onClick={(e) => e.stopPropagation()}>
            <img src={selectedFounder.image} alt={selectedFounder.name} style={popupImageStyle} />
            <div style={popupNameContainer}>
              <div style={popupNameStyle}>{selectedFounder.name}</div>
                <a href={selectedFounder.linkedin} target="_blank" rel="noopener noreferrer">
                  <GrLinkedin size={25} style={{margin:8, color:theme.colors.primary}}/>
                </a>
                {selectedFounder.twitter &&
                  <a href={selectedFounder.twitter} target="_blank" rel="noopener noreferrer">
                    <GrTwitter size={25} style={{marginLeft:8, color:theme.colors.primary}}/>
                  </a>
                }
            </div>
            <div style={popupTitleTextStyle}>{selectedFounder.title}</div>
            {selectedFounder.description.map((paragraph, index) => (
              <p style={popupDescriptionStyle} key={index}>{paragraph}</p>
            ))}
            <button style={closeButtonStyle} onClick={closePopup}>{theme.aboutus.closeButton}</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default AboutUs;
