import React, { useState, useEffect } from 'react';
import { theme } from '../../theme.js';

const Component = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };

    // Initial call to set initial state
    handleResize();

    // Add event listener for resize
    window.addEventListener('resize', handleResize);

    // Cleanup on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const containerStyle = {
    maxWidth: '800px',
    margin: '0 auto',
    // padding: '20px',
  };

  const titleStyle = {
    fontSize: '1rem',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const headingStyle = {
    textAlign: 'center',
    // marginBottom: '20px',
  };

  const rowStyle = {
    display: 'flex',
    justifyContent: 'space-between',
    flexWrap: 'wrap', // Wrap columns on smaller screens
    flexDirection: isMobile ? 'column' : 'row', // Switch to column layout for mobile
    marginBottom:'50px'
  };

  const columnStyle = {
    flex: '1',
    padding: '10px',
    textAlign: 'center',
    marginBottom: '-20px', // Space between columns in mobile view
    width: '100%', // Adjust width for mobile
  };

  const columnHeaderStyle = {
    fontSize: '2.5rem',
    fontWeight: '700',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
  };

  const columnDescriptionStyle = {
    fontSize: '1rem',
    color: theme.colors.primary,
    fontFamily: theme.fonts.primary,
    marginTop: '-30px', // Adjust spacing as needed
  };

  // Define columns as an array of objects
  const columns = theme.aboutus.numberColumns;

  return (
    <div style={containerStyle}>
      <div style={headingStyle}>
        <p style={titleStyle}>{theme.aboutus.numbersDescription}</p>
      </div>
      <div style={rowStyle}>
        {columns.map((column, index) => (
          <div key={index} style={columnStyle}>
            <p style={columnHeaderStyle}>{column.header}</p>
            <p style={columnDescriptionStyle}>{column.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Component;
